.video {
    &__player {
        width: 150px;
        aspect-ratio: 9/17.5;
        border-radius: 19px;
        overflow: hidden;
    }
    &__holder {
        position: absolute;
        top: 0;
        left: 0;
        background-image: url("../../../assets/iphone-mockup.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 100%;
        pointer-events: none;
    }
}