@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,600;6..12,700&display=swap');
ul {
	list-style: none;
	padding-left: 0;
}

@import "../../assets/scss/colors";
@import "../../assets/scss/responsive";

body {
	font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

.invalid {
	input {
		border: 0.1rem solid red;
		background-color: #3f1d1da4;
		transition: 300ms;
		&:hover {
			border: 0.1rem solid red;
			background-color: #303245;
		}
	}
	p {
		color: rgb(218, 63, 63);
		margin-top: 0;
		transition: 400ms;
	}
}

$black: #041c25;
$white: #ffffff;
$red: #ea5123;
$light-red: #ebad99;
$gray: #efebe6;

/* 3D Cube */
$cubeWidth: 70px;
$cubeHeight: 70px;
$cubeDepth: $cubeHeight;

._3d-wrap {
	position: relative;
	transform: scale(0.8);
	.image--3d {
		opacity: 0;
	}
	.space3d {
		perspective: 1000px;
		width: $cubeWidth;
		height: 100%;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-80%, 0);
		text-align: center;
		display: inline-block;
	}
	._3dbox {
		display: inline-block;
		transition: all 0.85s cubic-bezier(0.175, 0.885, 0.32, 1.275);

		text-align: center;
		position: relative;
		width: 100%;
		height: 100%;
		transform-style: preserve-3d;
		transform: rotateX(-15deg) rotateY(15deg);
		._3dface {
			overflow: hidden;
			position: absolute;

			border: 1px solid #888;
			background: #fff;
			box-shadow: inset 0 0 60px rgba(0, 0, 0, 0.1), 0 0 50px rgba(0, 0, 0, 0.3);
			color: #333;
			line-height: $cubeWidth;
			opacity: 0.8;
			img {
				height: 100%;
				width: auto;
			}
			&--front {
				// width: $cubeWidth;
				// height: $cubeHeight;
				height: 100%;
				width: auto;
				transform: translate3d(0, 0, calc($cubeDepth / 2));
			}
			&--top {
				width: 150px;
				height: $cubeDepth;
				transform: rotateX(90deg) translate3d(0, 0, calc($cubeHeight / 2));
			}
			&--left {
				width: $cubeDepth;
				height: $cubeHeight;
				left: 50%;
				height: 100%;
				margin-left: calc($cubeDepth / -2);
				transform: rotateY(-90deg) translate3d(0, 0, calc($cubeWidth / 2));
			}
		}
	}
}

.site {
    width: 100vw;
    overflow: hidden;
	background-color: $white;
	box-sizing: border-box;
	color: $black;
	a {
		color: $black;
	}
	font-family: "Nunito Sans", sans-serif, system-ui, -apple-system;
	&__title {
		color: $white;
		text-decoration: underline;
		text-decoration-color: $red;
		font-size: 39px;
		line-height: 49px;
	}

	&__description {
		padding-top: 2em;
		padding-bottom: 2em;
		.description__content {
			counter-reset: p-tag;
			p {
				counter-increment: p-tag;
				position: relative;
				margin-left: 2em;
				padding-bottom: 1em;
				border-bottom: 2px solid red;
				&:last-child {
					border-bottom: none;
				}
				&::before {
					content: "0" counter(p-tag) "\a0\a0";
					font-weight: 600;
					position: absolute;
					left: -2em;
					top: 0;
				}
			}
		}
	}

	&__faq {
		padding: 2em 0;
		&-item {
			border-top: 1px solid $white !important;
			padding-left: 0 !important;
			margin-left: 2em !important;
		}
	}
	$logo-height: 70px;
	&__logo {
		position: relative;
		display: block;
		&--image {
			display: flex;
			position: relative;
			justify-content: center;
			height: $logo-height;
			overflow: hidden;
			svg {
				height: $logo-height;
				width: auto;
			}
		}
	}
    .navigation {
        margin-right: 32px;
        padding-right: 8px;
        justify-content: center;
        @media screen and ( max-width: 900px ) {
            justify-content: flex-end;
        }
        .dropdown-menu {
            .handle {
                &::after {
                    width: 0; 
                    height: 0; 
                    border-left: 4px solid transparent;
                    border-right: 4px solid transparent;
                    border-top: 4px solid $black;
                    right: -8px;
                    position: absolute;
                    top: 16px;
                    translate: 0 -50%;
                }
            }
            .menu.menu--languages {
                display: none;
                position: relative;
                left: 0;
                margin: 0;
                &::after {
                    display: none;
                }
                li {
                    line-height: 32px;
                    cursor: pointer;
                }
                &.active {
                    display: flex;
                }
            }
        }
    }
    .unikbase-data {
        padding-top: 32px!important;
        padding-bottom: 0!important;
        @media screen and (max-width: 1000px) {
            > .row {
                align-items: center;
                #about-tpk .content {
                    margin-top: 0;
                    margin-bottom: 32px;
                    &::before {
                        width: 100%;
                    }
                }
                &:first-child {
                    flex-direction: column-reverse;
                }
            }
        }
    }
    
    #about-tpk {
        flex-grow: 1;
        margin-left: 32px;
        @media screen and ( max-width: 1000px ) {
            margin-left: 0;
        }
        .content {
            padding: 32px;
            position: relative;
            &::before {
                content: " ";
                position: absolute;
                width: 200%;
                height: 100%;
                background: $light-gray;
                z-index: 1;
                top: 0;
                left: 0;
            }
            > .col {
                z-index: 2;
                flex-basis: 80%;
            }
            h3 {
                color: $blue;
            }
            h4 {
                margin-top: 1em;
                font-size: 20px;
                line-height: 1.5em;
            }
        }
        .illustration {
            position: absolute;
            width: 25%;
            max-width: 560px;
            max-height: 560px;
            right: 0;
            top: -120px;
        }

    }
    #product {
        max-width: 380px;
        justify-content: center;
        flex-direction: column;
        display: flex;

        .thumbnail {
            height: auto;
            padding:  0 36px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            position: relative;
            &__wrap {
                width: 60%;
                aspect-ratio: 1;
                position: relative;
                overflow: hidden;
                background-repeat: no-repeat;
                background-size: 100% auto;
                background-position: center;
            }
            img {
                height: auto;
                width: 100%;
                top: 50%;
                left: 50%;
                position: absolute;
                translate: -50% -50%;
            }
        }
        .product {
            &__detail {
                height: 100%;
                padding: 36px;
                color: #505050;
                a {
                    color: #505050;
                }
                h3 {
                    margin-top: 0;
                }
                h4 {
                    margin: 0;
                }
                p {
                    margin-top: 0;
                }
            }
            &__title {
                font-size: 20px;
                text-align: center;
                margin: 12px 0 8px 0;
                line-height: 1.5em;
            }
            &_description {
                text-align: justify;
            }
        }

    }
    #pricing-plans {
        @media screen and (min-width: 1001px) {
            padding-top: 32px;
        }
        @media screen and (max-width: 1000px) {
            flex-wrap: wrap;
            .options {
                flex-basis: 100%;
            }
            .options + .col {
                flex-basis: 100%;
            }
        }
        flex-wrap: wrap;
        $item_height: 108px;
        .column__title {
            padding: 32px 0;
        }
        .options {
            background-color: $light-blue;
            h3 {
                margin: 0;
                font-size: 24px;
                font-weight: 600;
                line-height: 44px;
                color: $white;
                text-align: center;
                overflow: hidden;
                border-bottom: 2px solid $blue;
                padding: 0 36px;
            }
            .list {
                padding: 0 36px;
                margin-top: 1em;
