                        font-weight: 700;
                        white-space: nowrap;
                        background-color: rgba(255,255,255, 0.6);
                        border-bottom: 2px solid $blue;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        h3 {
                            display: flex!important;
                            flex-direction: column;
                            justify-content: center;
                            font-size: 1rem;
                            line-height: 2.618em;
                            text-transform: uppercase;
