                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                        }
                    }
    
                    // &__title .wrap::before,
                    // &__title .wrap::after,
                    // &__title::after,
                    // &__title::before {
                    // 	content: " ";
                    // 	width: 24px;
                    // 	height: 24px;
                    // 	position: absolute;
                    // 	top: 0;
                    // 	left: 0;
                    // 	border-width: 4px 0 0 4px;
                    // 	border-color: $black;
                    // 	border-style: solid;
                    // 	display: block;
                    // }
                    // &__title::before {
                    // 	left: auto;
                    // 	right: 0;
                    // 	border-width: 4px 4px 0 0;
                    // }
                    // &__title .wrap::before {
                    // 	top: auto;
                    // 	bottom: 0;
                    // 	border-width: 0 0 4px 4px;
                    // }
                    // &__title .wrap::after {
                    // 	top: auto;
                    // 	left: auto;
                    // 	bottom: 0;
                    // 	right: 0;
                    // 	border-width: 0 4px 4px 0;
                    // }
    
                    &__button {
                        background-color: $red;
                        border-radius: 24px;
                        text-transform: uppercase;
                        color: $white;
                        font-weight: 600;
                        font-size: 18px;
                        letter-spacing: -0.6px;
                        margin-top: 10px;
                        white-space: nowrap;
                        &:hover {
                            background-color: darken($red, 5%);
                        }
                        &:disabled {
                            background-color: #757575;
                        }
                    }
    
                    &__description {
                        padding-top: 1em;
                        padding-bottom: 1em;
                        background-color: $white;
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        .MuiSvgIcon-root {
                            margin-right: 0;
                        }
                        .option {
                            &__text {
                                // display: none;
                            }
                            @media screen and (max-width: 1000px) {
                                &__text {
                                    display: block;
                                }
                            }
                        }
                    }

                    &__actions {
                        padding-top: 12px;
