        .pricing-plan-note {
            margin-top:8px;
            font-size: 14px;
            font-style: italic;
            flex-basis: 100%;
            flex-shrink: 0;
        }
    }

    .video {
        &__player {
            position: relative;
            margin: auto;
            video {
                object-fit: contain;
                aspect-ratio: 1;
            }
            .play-button {
                position: absolute;
                top: 50%;
                left: 50%;
                translate: -50% -50%;
                width: 90px;
                height: 90px;
                display: block;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
            }

            &:not(.playing) {
                .play-button {
                    background-color: $blue;
                    .arrow {
                        margin-left: 4%;
                        width: 0; 
                        height: 0; 
                        border-top: 12px solid transparent;
                        border-bottom: 12px solid transparent;
                        border-left: 24px solid $white;
                    }
                }
            }
            &.playing {
                .play-button {
                    background-color: rgba($color: $white, $alpha: 0.5);
                    display: none;
                    .arrow {
                        width: 50%;
                        height: 50%;
                        background-color: rgba($color: $white, $alpha: 0.3);
                    }
                }
                &:hover .play-button {
                    display: flex;
                }
            }
        }
    }

    .section {
        margin-bottom: 32px;
        @media screen and (max-width: 1366px) { 
            max-width: calc(100% - 72px);
        }
        > .row {
            margin-left: -16px;
            margin-right: -16px;
            > .col {
                padding: 70px;
            }
        }
        h3 {
            font-size: 36px;
            line-height: 1.5em;
            font-weight: 700;
        }
        &--bg-gray {
            background-color: #EBEBEB;
        }
        &--bg-blue {
            background-color: $blue;
        }
        @media screen and (max-width: 1000px ){
            margin-bottom: 0;
            max-width: max-content;
            &:nth-child(2) {
                padding-top: 0;
            }
        }
    }
   
    #benefits {
        .list {
            .item {
                padding: 12px 0;
                border-bottom: 1px solid $white;
                &:last-child {
                    border-bottom: none;
                }
                span {
                    display: block;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 1.5em;
                }
            }
        }
        .benefit-cover-image {
            @media screen and (max-width: 1000px) {
                min-height: 50vw;
            }
        }
    }

    #about-digital-passport {
        &.section > .row > .col:first-child {
            padding: 32px;
        }
        p {
            margin-bottom: 0.618em;
        }
    }
}

/* Absolute Center Spinner */
.loading {
	position: fixed;
	z-index: 999;
	height: 2em;
	width: 2em;
	overflow: show;
	margin: auto;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

/* Transparent Overlay */
.loading:before {
	content: "";
	display: block;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: radial-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8));

	background: -webkit-radial-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8));
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
	/* hide "loading..." text */
	font: 0/0 a;
	color: transparent;
	text-shadow: none;
	background-color: transparent;
	border: 0;
}

.loading:not(:required):after {
	content: "";
	display: block;
	font-size: 10px;
	width: 1em;
	height: 1em;
	margin-top: -0.5em;
	-webkit-animation: spinner 1250ms infinite linear;
	-moz-animation: spinner 1250ms infinite linear;
	-ms-animation: spinner 1250ms infinite linear;
	-o-animation: spinner 1250ms infinite linear;
	animation: spinner 1250ms infinite linear;
	border-radius: 0.5em;
	-webkit-box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
	box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
}

/* Animation */

@-webkit-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@-moz-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@-o-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
